@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
.header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 1.5rem;
	background-color: var(--mainBackground);
	color: white;
}

.header__textDecoration {
	text-decoration: none;
}

.header__logo {
	font-size: 40px;
	color: #ececec;
	text-shadow: 0 0 25px rgba(255, 255, 255, 0.178);
	cursor: pointer;
}

.header__menu {
	display: flex;
	justify-content: space-around;
	align-items: center;
	list-style: none;
	font-size: 18px;
}

.header__link > * {
	margin: 0 0.6rem;
	color: var(--text);
	font-size: 20px;
}

.header__linkText:hover {
	color: white;
	text-shadow: 0 0 25px rgba(255, 255, 255, 0.288);
}

.header__mobile__item:hover {
	background-color: #333333;
}

@media only screen and (max-width: 768px) {
	.header__logo {
		font-size: 32px;
	}
}

.bi:hover {
  color: #026CCF;
}
.portfolio__container {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 80%;
}

.portfolio__container > * {
  margin: 2rem 0;
}

.portfolio__intro__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  min-height: 40vh;
  background-color: var(--portfolioIntroBackground);
}

.portfolio__intro__container > * {
  margin: 1rem 0;
}

.portfolio__intro__title {
  font-size: 50px;
  color: white;
}

/* Portfolio Grid Layout */
.portfolio__main__container {
  text-align: center;
}

.portfolio__main__container > * {
  margin: 1.5rem 0;
}

.portfolio__main__title {
  font-size: 32px;
  color: rgba(245, 245, 245, 0.856);
}

.portfolio__grid__title {
  padding: 0.5rem;
  color: rgba(245, 245, 245, 0.719);
  font-size: 24px;
  letter-spacing: 1px;
  background-color: #21252c;
  border-radius: 15px 15px 0 0;
}

.portfolio__grid__image {
  max-height: 100%;
  max-width: 100%;
  border-radius: 0 0 15px 15px;
}

.portfolio__grid__image:hover {
  background-color: #4065a7;
}

.portfolio__grid__overlay {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: transparent;
  border-radius: 0 0 15px 15px;
  cursor: default;
  z-index: 3;
  padding: 1rem;
  display: none;
}

.overlay__item {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.portfolio__grid__item:hover .portfolio__grid__overlay {
  display: block;
  background-color: #21252cbb;
}

.portfolio__grid__text {
  text-align: left;
  color: whitesmoke;
  overflow: hidden;
}

.portfolio__grid__github {
  color: #e9e9e9;
}

.portfolio__grid__github:hover {
  color: #026ccf;
}

.contact__title {
  font-size: 32px;
  border-bottom: 2px solid grey;
  border-radius: 1px;
  width: 110px;
  margin: 0 auto .5rem auto;
}

.contact__btn__container {
  text-decoration: none;
  display: flex;
  justify-content: center;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:focus {
  outline: none;
}

:root {
  --mainBackground: #0F1217;
  --mainChildrenBackground: #0E1A36;
  /* --headerBackground: #24315E;
  --portfolioIntroBackground: #303C6C;
  --portfolioGridBackground: #439684;
  --contactFormBackground: #303C6C; */
  --text: #fcfcfcbb;
  --btn: #4978e7;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.App {
  font-family: Roboto;
  background-color: #0F1217;
  height: 100vh;
  overflow-y: auto;
}

.contact__container {
  max-width: 50%;
  margin: 7rem auto;
  padding: 1.5rem;
  background-color: "#e9e9e9";
}

@media only screen and (max-width: 1168px) {
  .contact__container {
    max-width: 65%;
  }
}

@media only screen and (max-width: 1068px) {
  .contact__container {
    max-width: 80%;
  }
}
